:root {
  --col-beige-dark: hsl(60, 17%, 80%);
  --col-beige-mid: hsl(45, 57%, 88%);
  --col-beige-light: hsl(40, 60%, 95%);
  --col-brown-mid: hsl(32, 11%, 23%);
  --col-black-dark: hsl(34, 7%, 19%);
  --col-black-light: hsl(33, 14%, 25%);
  --col-grey-dark: hsl(200, 6%, 42%);
  --col-grey-light: hsl(200, 8%, 70%);
  --col-offwhite: hsl(60, 17%, 91%);
  --col-white: hsl(0, 0%, 100%);
  --col-olive-extra-dark: hsl(60, 15%, 33%);
  --col-olive-dark: hsl(60, 17%, 48%);
  --col-olive-mid: hsl(60, 18%, 69%);
  --col-olive-light: hsl(60, 20%, 74%);
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
}

ol,
ul {
  list-style: none;
}

img,
video {
  width: 100%;
}

a {
  text-decoration: none;
}

:root {
  --space-xxxs: 0.1rem;
  --space-xxs: 0.25rem;
  --space-xs: 0.5rem;
  --space-sm: 0.75rem;
  --space-md: 1rem;
  --space-lg: 1.25rem;
  --space-xl: 2rem;
  --space-xxl: 3.25rem;
  --space-xxxl: 5.25rem;
}

:root {
  --font-big-daily: "big-daily-short";
  --font-clash-display: "clash-display";
  --font-surt: "surt";
  --text-base-size: 1rem;
  --text-xs: calc(var(--text-base-size) * 0.75);
  --text-sm: calc(var(--text-base-size) * 0.875);
  --text-md: calc(var(--text-base-size));
  --text-lg: calc(var(--text-base-size) * 1.5);
  --text-xl: calc(var(--text-base-size) * 2);
  --text-xxl: calc(var(--text-base-size) * 2.75);
  --text-xxxl: calc(var(--text-base-size) * 3.25);
  --text-mega: calc(var(--text-base-size) * 7);
  --text-huge: calc(var(--text-base-size) * 14);
  --letter-space-sm: 0.125rem;
  --letter-space-md: 0.5rem;
  --letter-space-lg: 1rem;
  --line-space-base: 1;
}

body {
  font-family: var(--font-surt);
  color: var(--col-brown-mid);
  line-height: 1.2;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-clash-display);
  font-weight: 300;
}

.text-huge {
  font-size: var(--text-huge);
}

.text-mega {
  font-size: var(--text-mega);
}

.text-xxxl {
  font-size: var(--text-xxxl);
}

.text-xxl {
  font-size: var(--text-xxl);
}

.text-xl {
  font-size: var(--text-xl);
}

.text-lg {
  font-size: var(--text-lg);
}

.text-md {
  font-size: var(--text-md);
}

.text-sm {
  font-size: var(--text-sm);
}

.text-xs {
  font-size: var(--text-xs);
}

p {
  font-size: var(--text-md);
}

a {
  color: var(--col-brown-mid);
}

.col-light {
  color: var(--col-offwhite);
}

.col-dark {
  color: var(--col-brown-mid);
}

.col-olive {
  color: var(--col-olive-mid);
}

.bold {
  font-weight: bold;
}

.big-display {
  font-family: var(--font-big-daily);
}

.clash-display {
  font-family: var(--font-clash-display);
}

.surt {
  font-family: var(--font-surt);
}

.space-sm {
  letter-spacing: var(--letter-space-sm);
}

.space-md {
  letter-spacing: var(--letter-space-md);
}

.space-lg {
  letter-spacing: var(--letter-space-lg);
}

.lineheight-base {
  line-height: var(--line-space-base);
}

@font-face {
  font-family: "big-daily-short";
  src: url("../../dist/fonts/BigDailyShort-LightItalic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "clash-display";
  src: url("../../dist/fonts/ClashDisplay-Medium.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "surt";
  src: url("../../dist/fonts/Surt-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.icon-btn {
  border: none;
  outline: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}
.icon-btn img {
  width: 0.9375rem;
}

.main-hero {
  position: relative;
  display: flex;
  justify-content: center;
  padding-bottom: var(--space-sm);
}
.main-hero__video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
}
.main-hero__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
}

.sub-hero {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../../dist/images/about/about-hero.jpg");
  display: flex;
  flex-direction: column;
  margin-bottom: var(--space-xl) !important;
}
.sub-hero__content {
  position: relative;
  padding: var(--space-md) var(--space-xl);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex: 1;
}

header,
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-md) var(--space-xl) 0;
  width: 100%;
}

.intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.intro__images {
  margin-block: var(--space-md);
  display: flex;
  gap: var(--space-sm);
  height: 35vh;
}
.intro__images img {
  object-fit: cover;
  width: 35vh;
}
.intro__footnote {
  display: flex;
  align-items: center;
  gap: var(--space-xl);
}
.intro__footnote-divider {
  height: 1px;
  width: 20rem;
  background-color: var(--col-brown-mid);
}

.usp {
  display: flex;
  gap: var(--space-md);
  margin-bottom: var(--space-xxl) !important;
}
.usp__gallery {
  width: 65%;
  display: grid;
  grid-template-rows: 100vh 150px 350px repeat(2, 150px) 250px 250px 350px;
  grid-template-columns: repeat(5, 1fr);
}
.usp__gallery img,
.usp__gallery video {
  height: 100%;
  object-fit: cover;
}
.usp__gallery :nth-child(1) {
  grid-column: 1/5;
  grid-row: 1/2;
}
.usp__gallery :nth-child(2) {
  grid-column: 5/6;
  grid-row: 2/3;
}
.usp__gallery :nth-child(3) {
  grid-column: 3/5;
  grid-row: 3/4;
}
.usp__gallery :nth-child(4) {
  grid-column: 1/2;
  grid-row: 4/5;
}
.usp__gallery :nth-child(5) {
  grid-column: 5/6;
  grid-row: 5/6;
}
.usp__gallery :nth-child(6) {
  grid-column: 3/5;
  grid-row: 6/7;
}
.usp__gallery :nth-child(7) {
  grid-column: 1/3;
  grid-row: 7/8;
}
.usp__gallery :nth-child(8) {
  grid-column: 3/5;
  grid-row: 8/9;
}
.usp__quote {
  width: 35%;
}

.highlighted {
  padding-bottom: 0 !important;
  margin-bottom: var(--space-xxl) !important;
}
.highlighted__product {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.highlighted__product-title {
  display: flex;
  gap: var(--space-xl);
  margin-bottom: var(--space-sm);
}
.highlighted__product-banner {
  flex: 1;
}

.projects__title {
  display: flex;
  align-items: flex-start;
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
}
.projects__title-icons {
  display: flex;
  gap: var(--space-md);
  flex-direction: column;
  align-items: space-between;
}
.projects__title-icons img {
  width: 1.75rem;
}
.projects__grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(3, 130px);
  column-gap: calc(1.5 * var(--space-xxxl));
  row-gap: var(--space-xxxl);
}
.projects__grid img {
  height: 100%;
  object-fit: cover;
}

.contact {
  padding-bottom: var(--space-xxl);
  display: flex;
  justify-content: center;
  background-image: url("../../dist/images/homepage/desja-banner.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  align-items: flex-end;
}
.contact__card {
  width: 50rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  padding: var(--space-xl);
  background-color: var(--col-beige-light);
  height: 80%;
}
.contact__card div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-xxxl);
}

footer,
.footer {
  padding: 0 var(--space-xl) var(--space-lg);
  height: 80vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  background-color: var(--col-grey-light);
}
footer__links,
.footer__links {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: var(--col-beige-light);
  display: flex;
  justify-content: center;
  z-index: 999;
}
.menu--hidden {
  display: none;
}
.menu__close-btn {
  position: absolute;
  top: 0;
  right: 0;
  padding: var(--space-xl);
}
.menu__nav-links {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}
.menu__nav-link div {
  display: flex;
  align-items: flex-start;
  gap: var(--space-sm);
}
.menu__preview-img {
  width: 31.25rem;
  height: 37.5rem;
}
.menu__preview-img img {
  height: 100%;
  object-fit: cover;
}

.cases {
  display: flex;
  justify-content: center;
  margin-bottom: var(--space-sm) !important;
}
.cases__carousel {
  margin-top: var(--space-md);
  position: relative;
  display: flex;
  justify-content: center;
  min-height: 45rem;
  width: 100%;
}
.cases__carousel-item {
  object-fit: cover;
  position: absolute;
  max-width: 40rem;
  max-height: 45rem;
  overflow: hidden;
}

.our-work {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: calc(var(--space-xxxl) * 1.5);
}
.our-work :nth-child(1) {
  max-width: 55rem;
}
.our-work :nth-child(2) {
  max-width: 50rem;
}

.what-we-do {
  background-color: var(--col-grey-light);
  margin-bottom: var(--space-xxl) !important;
}
.what-we-do__intro {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-block: var(--space-xxxl) var(--space-xl);
}
.what-we-do__list-card {
  background-color: var(--col-beige-light);
  padding: var(--space-md) var(--space-xxl) var(--space-xxxl);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: calc(var(--space-xxl) * 3.5);
  text-align: center;
  z-index: 10;
}
.what-we-do__list {
  display: flex;
  flex-direction: column;
  gap: var(--space-xxs);
}
.what-we-do__backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 12.5rem;
}
.what-we-do__backdrop img {
  height: 100%;
  object-fit: cover;
}
.what-we-do__content {
  padding-block: calc(var(--space-xxxl) * 2);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: var(--space-xxxl);
}
.what-we-do__content > p {
  max-width: 55rem;
}
.what-we-do__images {
  display: flex;
  gap: var(--space-sm);
  max-width: 40rem;
}
.what-we-do__images img {
  width: 50%;
  object-fit: cover;
}

.who-we-are {
  background-color: var(--col-olive-light);
}
.who-we-are__team-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: calc(var(--space-xxxl) * 2);
  padding: var(--space-xl) calc(var(--space-xxxl) * 2) var(--space-xxxl);
}
.who-we-are__team-member {
  margin-bottom: var(--space-xxl);
}
.who-we-are__team-member:nth-of-type(even) {
  position: relative;
  top: 50%;
}
.who-we-are__member-photo {
  max-height: 600px;
  object-fit: cover;
  margin-bottom: var(--space-md);
}

.featured {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: calc(var(--space-xxxl) * 2);
  text-align: center;
  margin-bottom: var(--space-xl) !important;
}
.featured__brand-slider {
  display: flex;
  gap: calc(var(--space-xxxl) * 1.5);
  overflow: hidden;
  max-width: 100%;
}
.featured__slider-item {
  min-width: 12rem;
  display: flex;
  align-items: center;
}
.featured__slider-item img {
  max-height: 4rem;
}

.studio {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-image: url("../../dist/images/about/studio-backdrop.jpg");
  display: flex;
  justify-content: center;
  padding-block: calc(var(--space-xxxl) * 3) calc(var(--space-xxxl) * 2);
}
.studio__card {
  background-color: var(--col-beige-light);
  display: flex;
  flex-direction: column;
  gap: calc(var(--space-xxxl) * 2);
  align-items: center;
  max-width: 49rem;
  text-align: center;
  padding: var(--space-xxxl) var(--space-xxxl) var(--space-xl);
}
.studio__card-img {
  position: relative;
  max-width: 25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.studio__card-img img {
  width: 70%;
  margin-block: -3.5rem;
}
.studio__card-img span {
  z-index: 10;
}

.message {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
  margin-bottom: var(--space-xl) !important;
}
.message__offset-content {
  max-width: 65rem;
  align-self: flex-end;
}
.message__image {
  display: flex;
  align-items: flex-end;
  gap: var(--space-md);
}
.message__image img {
  margin-bottom: var(--space-md);
  width: 25rem;
}

.container {
  margin: 0 auto;
  max-width: 100rem;
}
.container--padding {
  padding: var(--space-xl);
}
.container--full-width {
  max-width: 100%;
}
.container--fit-viewportheight {
  height: 100vh;
  overflow: hidden;
}

body {
  background-color: var(--col-offwhite);
}